import React from 'react';
import { default as MuiCardMedia, CardMediaProps } from '@mui/material/CardMedia';

const CardMedia = React.forwardRef<HTMLElement, CardMediaProps>( ( props, ref ) => {
	return (
		<MuiCardMedia
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<CardMediaProps>;

export default CardMedia;
